@use "/src/styles/variables";

@mixin breakpoint-mobile-down {
    @media (max-width: variables.$mobile_down) {
        @content;
    }
}
@mixin breakpoint-mobile-up {
    @media (min-width: variables.$mobile_up) {
        @content;
    }
}
